.mobile_field {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
    padding: 20px 0;

}

.inputs_value {
    margin-top: 20px;
}

.span_open {
    cursor: pointer;
    color: #efefef;
    border: 1px solid #cecece;
    padding: 5px 10px;
    border-radius: 20px;
    width: fit-content;
    margin-top: 15px;
}

.radio_btn {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.form {
    height: 0;
    transition: .5s all ease-out;
    overflow: hidden;
}

.text_area {
    width: 100%;
    height: 90px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    outline: none;
    border: none;
    padding: 10px 10px;
    color: #fff;
    font-size: 19px;
    margin: 15px 0 15px 0;
}

.text_area::-webkit-input-placeholder {
    font-family: Roboto, 'sans-serif';
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    color: #949494;
}

.span {
    cursor: pointer;
    color: #efefef;
    border: 1px solid #cecece;
    padding: 5px 10px;
    border-radius: 20px;
    margin-left: 5px;
}

.info_item {
    margin-top: 20px;
}

.info_item:first-child {
    margin-top: 0;
}

.info_item span {
    padding: 5px 10px;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 20px
}

.info_item span:last-child {
    margin-left: 10px;
}

.input input {
    height: 50px;
    width: 100%;
    background: rgba(0, 0, 0, 0.5);
    border-radius: 10px;
    outline: none;
    border: none;
    padding: 0 10px;
    color: #fff;
    font-size: 19px;
    margin: 15px 0 15px 0;
}

.input input::-webkit-input-placeholder {
    font-family: Roboto, 'sans-serif';
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    color: #949494;
}